import React, { useState } from "react";
import { IoCloseOutline } from "react-icons/io5";
import { useModal } from "../../../context/ModalProvider";
import Overlay from "../Overlay";
import "./styles.css";
import Select from "react-select";

export interface FiltroProps {
  changePrice: (price: number) => void;
  changeDistance: (distance: number) => void;
  currentPrice: number;
  currentDistance: number;
}

const Filtro = ({
  changePrice,
  changeDistance,
  currentPrice,
  currentDistance,
}: FiltroProps) => {
  const { filter } = useModal();
  let [price, setPrice] = useState<number>(currentPrice);
  let [distance, setDistance] = useState<number>(currentDistance);

  const prices = [
    {
      label: "At R$ 100",
      value: 100,
    },
    {
      label: "At R$ 200",
      value: 200,
    },
    {
      label: "At R$ 300",
      value: 300,
    },
    {
      label: "At R$ 400",
      value: 400,
    },
    {
      label: "At R$ 500",
      value: 500,
    },
  ];

  const distances = [
    {
      label: "At 10km",
      value: 10,
    },
    {
      label: "At 20km",
      value: 20,
    },
    {
      label: "At 40km",
      value: 40,
    },
    {
      label: "At 60km",
      value: 60,
    },
    {
      label: "At 80km",
      value: 80,
    },
    {
      label: "At 100km",
      value: 100,
    },
    {
      label: "Independente da distncia",
      value: 9999,
    },
  ];

  function handleFilter() {
    changePrice(price);
    changeDistance(distance);

    filter.close();
  }

  return (
    <Overlay>
      <form className="filtro">
        <div onClick={filter.close} className="close-button">
          <IoCloseOutline size={"4rem"} color="#fff" />
        </div>
        <h1>Filtros</h1>
        <div className="filtro-select">
          <div className="filtro-select-unique">
            <p>Preos</p>
            <Select
              options={prices}
              defaultValue={{
                label: `At R$ ${currentPrice}`,
                value: currentPrice,
              }}
              onChange={(e: any) => {
                setPrice(e.value);
              }}
            />
          </div>
          <div className="filtro-select-unique">
            <p>Distncia</p>
            <Select
              options={distances}
              defaultValue={
                !currentDistance
                  ? distances[distances.length - 1]
                  : distances.filter(
                      (item) => item.value === currentDistance
                    )[0]
              }
              onChange={(e: any) => {
                setDistance(e.value);
              }}
            />
          </div>

          <button
            type="button"
            className="filtro-button"
            onClick={handleFilter}
          >
            Filtrar
          </button>
        </div>
      </form>
    </Overlay>
  );
};

export default Filtro;
