interface User {
  firstName: string;
  lastName: string;
  email: string;
  password: string;
  confirmPassword: string;
  phoneNumber: string;
  image: string;
  birthDate: string;
}

export default function validateInfo(credentials: User) {
  let errors = {} as User;

  if (credentials !== null) {
    if (!credentials?.firstName?.length ?? 0) {
      errors.firstName = "Campo de nome  necessrio";
    }

    if (!credentials?.lastName?.trim()) {
      errors.lastName = "Campo de sobrenome  necessrio";
    }

    if (!credentials?.email) {
      errors.email = "Campo de e-mail  necessrio";
    } else if (
      !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(credentials?.email)
    ) {
      errors.email = "E-mail invlido";
    }

    if (!credentials?.password) {
      errors.password = "Campo de senha  necessrio";
    } else if (credentials?.password.length < 6) {
      errors.password = "A senha precisa de 6 ou mais caracteres";
    }

    if (!credentials?.confirmPassword) {
      errors.confirmPassword = "Campo de confirmao de senha  necessrio";
    } else if (credentials.confirmPassword !== credentials.password) {
      errors.confirmPassword = "As senhas no combinam";
    }

    const phoneNumbers = credentials?.phoneNumber?.replace(/[-.() ]/g, "");

    if (isNaN(parseInt(phoneNumbers))) {
      errors.phoneNumber = "Nmero de telefone invlido";
    } else if (phoneNumbers.length !== 11) {
      errors.phoneNumber = "Nmero de telefone invlido";
    } else if (!phoneNumbers.length ?? 0) {
      errors.phoneNumber = "Campo de telefone  necessrio";
    }

    if (!credentials?.birthDate?.length ?? 0) {
      errors.birthDate = "Campo de data de nascimento  necessrio";
    } else {
      let year = new Date(credentials?.birthDate).getFullYear();
      let month = new Date(credentials?.birthDate).getMonth() + 1;
      let day = new Date(credentials?.birthDate).getDate();
      if (new Date().getFullYear() - year >= 18) {
        if (new Date().getFullYear() - year === 18) {
          if (new Date().getMonth() + 1 - month >= 0) {
            if (new Date().getDate() - day <= 0) {
              errors.birthDate = "Voc precisa ser maior de 18 anos";
            }
          } else {
            errors.birthDate = "Voc precisa ser maior de 18 anos";
          }
        }
      } else {
        errors.birthDate = "Voc precisa ser maior de 18 anos";
      }
    }
  }

  return errors;
}
