import React from "react";
import "./styles.css";
import image404 from "../../assets/images/404.svg";
import { Link } from "react-router-dom";

const Page404 = () => {
  return (
    <div className="page-404">
      <h1>Pgina no encontrada...</h1>
      <img src={image404} />
      <p>
        Pgina ou usurio no encontrado. Podem ter sido excludos, atualizados
        ou nem se quer ter existido.
      </p>
      <Link to="/">Voltar para pgina principal</Link>
    </div>
  );
};

export default Page404;
